.playground-outer-container {
  margin-top: 5rem;
  padding: 0rem 8rem;
  margin-bottom: 15rem;

  .headline {
    padding: 2rem 0rem;
    font-size: 3rem;
    font-weight: 700;
    color: rgb(77, 77, 78);
  }
}

.playground-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: stretch;
  justify-items: stretch;
  column-gap: 1rem;
  row-gap: 1rem;
}

.iframe-element {
  height: 500px;
}

@media only screen and (max-width: 900px) {
  .playground-outer-container {
    padding: 0rem 3rem;
  }
  .headline {
    text-align: center;
  }
  .playground-container {
    display: grid;
    grid-template-columns: 1fr;
    align-items: stretch;
  }
}